  grid-row-end: 2;
  grid-column-start: 2;
  grid-column-end: 3; 
  
  this property allow us to Shift any div accordingly as per Requirement.
  */
}

.childGrid {
  display: grid;
  grid-template-columns: 60px 1fr;
  grid-template-rows: 40px 40px;
  border-radius: 20px;
}
